/*===========================
    1. COMMON css 
===========================*/
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,500,600,700,800&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700,900&display=swap");

// Font Family
$montserrat: 'Montserrat',
sans-serif;
$lato: 'Lato',
sans-serif;

// Colors ---------------
$white: #fff;
$black: #222;
$grey:#f5f5f5;
$theme-color: #3764eb;
$theme-color-2: #18152c;
$body-text-color: #777;
$border-color:#e2e2e2;
$border-color-2:#e7e7e7;
$border-color-3:#ccc;


// Heading Color
$heading-color: #333;

// Soft color
$black-soft:#7e7e7e;


* {
	margin: 0;
	padding: 0;
}

body {
	font-family: $lato;
	font-weight: 400;
	font-style: normal;
	color: $body-text-color;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

img {
	max-width: 100%;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
	text-decoration: none;
	outline: none;
}

a:focus,
a:hover {
	text-decoration: none;
}

i,
span,
a {
	display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $lato;
	font-weight: 700;
	color: $heading-color;
	margin: 0px;
}

h1 {
	font-weight: 500;
	font-size: 60px;
}

h2 {
	font-size: 36px;
}

h3 {
	font-size: 28px;
}

h4 {
	font-size: 22px;
}

h5 {
	font-size: 18px;
}

h6 {
	font-size: 16px;
}

ul,
ol {
	margin: 0px;
	padding: 0px;
	list-style-type: none;
}

p {
	font-size: 15px;
	font-weight: 400;
	line-height: 26px;
	margin: 0px;
}

ul,
li,
a {
	text-decoration: none;
	list-style: none;
}

.clear-fix {
	clear: both;
}

//parallax css
.parallax-window {
	min-height: 100vh;
	background: transparent;
}